  & .nav-link,\n  & .nav-leaf {\n    overflow: clip;\n    text-overflow: ellipsis;\n\n    & p {\n      white-space: nowrap;\n    }\n  }\n}\n\nbody.dark-mode .nav-sidebar .nav-header.nav-divider {\n  color: $light-dark;\n\n  &::before,\n  &::after {\n    border-top: 1px solid $light-dark;\n  }\n}\n\n.sidebar-collapse .nav-sidebar .nav-header.nav-divider {\n  text-align: right;\n  &::before {\n    border-top: transparent!important;\n  }\n}\n\n.ravedash-back-to-top,\n.back-to-top {\n  // opacity: 0.4;\n  // transition: opacity 200ms;\n  bottom: 1.25rem;\n  position: fixed;\n  right: 1.25rem;\n  z-index: 1039;\n  box-shadow: 0 0 10px 1px adjust-color($gray, $alpha: -0.5);\n  border-radius: .25rem;\n\n  &.show,\n  &:hover {\n    opacity: 1;\n  }\n\n  & .dropdown-menu {\n    margin-bottom: 0;\n  }\n\n  &:focus {\n    box-shadow: none;\n  }\n\n  &.left {\n    right: auto;\n    left: 1.25rem;\n\n    & .rave-button-autorecalculate {\n      background-color: $yellow;\n      // adjust-color($color: $yellow, $lightness: 25%);\n    }\n  }\n}\n\n\n\n\n// code-display\n.info-box {\n  & .code-display {\n    @include padding($size: 0);\n    @include margin($size: -0.5, $unit: 1em);\n  }\n  & .info-box-content {\n    @include fill-width();\n  }\n}\n\n.code-display pre {\n  @include padding($size: 0);\n}\n\n.card-expanded .output-wrapper {\n  position: relative;\n  z-index: 1040;\n}\n\n.card-footer.code-display .info-box {\n  @include border-radius(0);\n}\n\n// card-accordion\n.card-accordion {\n  &.catd-tools {\n    @include position($type: absolute);\n    right: 0;\n    margin: 7px 5px; // TODO: change this\n  }\n  &.card-header {\n    @include padding($size: 0);\n    @include position($type: relative);\n\n    & .card-accordion-title {\n      @include padding($size: 10);\n    }\n\n    & [data-toggle=\"collapse\"] {\n      cursor: pointer;\n    }\n  }\n}\n\n.card-header > .card-title {\n  & > .form-group {\n    display: inline-block;\n    margin: 0;\n\n    & label,\n    & label > span {\n      font-size: 1.1rem;\n      font-weight: 400 !important;\n    }\n  }\n}\n\n// card2\n.card2-body {\n  @include overflow(hidden, x);\n  @include overflow(auto, y);\n\n  & .direct-chat-contacts {\n\n    background-color: adjust-color($gray-dark, $alpha: -0.5);\n\n    & hr {\n      @include border-color( rgb(232,232,232), top );\n    }\n  }\n}\n\n// group input box\n.group-input-box {\n  border: 1px dashed $light-gray;\n  padding-top: 0.6rem;\n  margin-top: 1.5rem;\n  margin-bottom: 1.5rem;\n  position: relative;\n\n  &::before {\n    content: attr(title);\n    position: absolute;\n    margin-top: -1.5rem;\n    left: 0.4rem;\n    color: $gray; // $light-dark;\n    background-color: $light;\n    padding: 0.1rem 0.4rem;\n  }\n}\n.dark-mode .group-input-box {\n  border: 1px dashed $light-dark;\n  &::before {\n    color: $light-gray;\n    background-color: $light-dark;\n  }\n}\n\n// progress-bar\n.progress-error { @include display(none, true); }\n.progress-message { @include display(block); }\n.shidashi-progress-error {\n  & .progress-error {\n    @include display(block, true);\n    color: $color-error !important;\n  }\n  & .progress-message {\n    @include display(none);\n  }\n  & .progress-bar {\n    background-color: $color-error !important;\n  }\n}\n\n// flip-box\n.flip-box {\n  background-color: transparent;\n  border: none;\n  perspective: 1000px;\n  padding: 0 !important;\n  position: relative;\n\n  &[data-toggle=\"click\"] .flip-box-front,\n  &[data-toggle=\"click-front\"] .flip-box-front {\n    cursor: pointer;\n  }\n\n  & .flip-box-inner {\n    position: relative;\n    height: 100%;\n    width: 100%;\n    margin: 0 !important;\n    padding: 0 !important;\n    transition: transform 0.5s;\n    transition-timing-function: cubic-bezier(1, 0, 1, 1);\n    transform-style: preserve-3d;\n\n    & .flip-box-front,\n    & .flip-box-back {\n      width: 100%;\n      margin: 0 !important;\n      padding: 0 !important;\n      -webkit-backface-visibility: hidden;\n      backface-visibility: hidden;\n    }\n    & .flip-box-front {\n    \tposition: relative;\n      transform: rotateY(360deg);\n    }\n    & .flip-box-back {\n      position: absolute;\n      z-index: 1038;\n      transform: rotateY(180deg);\n    }\n  }\n\n  &.resize-vertical {\n    min-height: 1px;\n\n    & .flip-box-inner {\n      position: absolute;\n      @include fill-height();\n    }\n\n  }\n\n  &.fill .flip-box-inner,\n  &.fill .flip-box-front {\n    @include fill-height();\n    @include fill-width();\n  }\n\n  &.active {\n    z-index: 1;\n  }\n  &.active .flip-box-inner {\n    transform: rotateY(180deg);\n  }\n}\n\n.card-body .flip-box {\n  & .flip-box-inner .flip-box-back {\n    max-height: 100%;\n    max-width: 100%;\n    &, & * {\n      overflow: hidden;\n    }\n  }\n  &.active .flip-box-inner .flip-box-back {\n    overflow-y: auto;\n  }\n}\n\n// shiny-notification\n.toasts-top-right {\n  // needed to stay above modal\n  z-index: 1060;\n}\n\n#shiny-notification-panel {\n  width: 350px;\n  background-clip: padding-box;\n  bottom: 70px;\n  @include border-radius(0.25, 1rem);\n\n  & .shiny-notification-close {\n    @include visibility(hidden);\n    color: #adb5bd;\n    font-size: 24px;\n  }\n\n  & .shiny-notification {\n    background-color: rgba(52,58,64);\n    border: 1px solid rgba(0,0,0,0.1);\n    box-shadow: 0 .25rem .75rem rgba(0,0,0,.1);\n    color: #fff;\n    opacity: 0.9;\n\n    & .progress {\n      height: 3px;\n      background: #eee;\n      margin-top: 24px;\n      margin-bottom: -30px;\n\n      & .progress-bar {\n        background: $primary;\n      }\n    }\n\n    & .progress-text .progress-message {\n      display: block;\n      margin-bottom: 10px;\n      width: 100%;\n      height: 20px;\n      overflow: clip;\n      text-overflow: clip;\n    }\n  }\n}\n\nbody.dark-mode #shiny-notification-panel .shiny-notification {\n  background-color: rgba(255,255,255);\n  color: #1f2d3d;\n}\n\n/* Additional shiny styles */\n.dark-mode {\n  & .selectize-con