footer-height;\n    display: none;\n    right: -$control-sidebar-width;\n    width: $control-sidebar-width;\n    @include transition(\n      right $transition-speed $transition-fn,\n      display $transition-speed $transition-fn\n    );\n  }\n\n  &::before {\n    content: \"\";\n    display: block;\n    position: fixed;\n    top: 0;\n    z-index: -1;\n  }\n}\n\nbody.text-sm {\n  .control-sidebar {\n    bottom: $main-footer-height-sm;\n    top: $main-header-height-sm;\n  }\n}\n\n.main-header.text-sm ~ .control-sidebar {\n  top: $main-header-height-sm;\n}\n\n.main-footer.text-sm ~ .control-sidebar {\n  bottom: $main-footer-height-sm;\n}\n\n.control-sidebar-push-slide {\n  .content-wrapper,\n  .main-footer {\n    @include transition(margin-right $transition-speed $transition-fn);\n  }\n}\n\n// Control sidebar open state\n.control-sidebar-open {\n  .control-sidebar {\n    display: block !important;\n\n    &,\n    &::before {\n      right: 0;\n    }\n  }\n\n  &.control-sidebar-push,\n  &.control-sidebar-push-slide {\n    .content-wrapper,\n    .main-footer {\n      margin-right: $control-sidebar-width;\n    }\n  }\n}\n\n// Control sidebar slide over content state\n.control-sidebar-slide-open {\n  .control-sidebar {\n    display: block;\n\n    &,\n    &::before {\n      right: 0;\n      @include transition(\n        right $transition-speed $transition-fn,\n        display $transition-speed $transition-fn\n      );\n    }\n  }\n\n  &.control-sidebar-push,\n  &.control-sidebar-push-slide {\n    .content-wrapper,\n    .main-footer {\n      margin-right: $control-sidebar-width;\n    }\n  }\n}\n\n// Dark skin\n.control-sidebar-dark {\n  background-color: $sidebar-dark-bg;\n\n  &,\n  a,\n  .nav-link {\n    color: $sidebar-dark-color;\n  }\n\n  a:hover {\n    color: $sidebar-dark-hover-color;\n  }\n\n  // Headers and labels\n  h1,\n  h2,\n  h3,\n  h4,\n  h5,\n  h6,\n  label {\n    color: $sidebar-dark-hover-color;\n  }\n\n  // Tabs\n  .nav-tabs {\n    background-color: $sidebar-dark-hover-bg;\n    border-bottom: 0;\n    margin-bottom: 5px;\n\n    .nav-item {\n      margin: 0;\n    }\n\n    .nav-link {\n      border-radius: 0;\n      padding: 10px 20px;\n      position: relative;\n      text-align: center;\n\n      &,\n      &:hover,\n      &:active,\n      &:focus,\n      &.active {\n        border: 0;\n      }\n\n      &:hover,\n      &:active,\n      &:focus,\n      &.active {\n        border-bottom-color: transparent;\n        border-left-color: transparent;\n        border-top-color: transparent;\n        color: $sidebar-dark-hover-color;\n      }\n\n      &.active {\n        background-color: $sidebar-dark-bg;\n      }\n    }\n  }\n\n  .tab-pane {\n    padding: 10px 15px;\n  }\n}\n\n// Light skin\n.control-sidebar-light {\n  color: lighten($sidebar-light-color, 10%);\n\n  //  Background\n  background-color: $sidebar-light-bg;\n  border-left: $main-header-bottom-border;\n}\n","//\n// Component: Dropdown\n//\n\n// General Dropdown Rules\n//.dropdown-item {\n//  &:first-of-type {\n//    @include border-top-radius($border-radius);\n//  }\n//  &:last-of-type {\n//    @include border-bottom-radius($border-radius);\n//  }\n//}\n\n.text-sm {\n  .dropdown-menu {\n    font-size: $font-size-sm !important;\n  }\n\n  .dropdown-toggle::after {\n    vertical-align: .2rem;\n  }\n}\n\n.dropdown-item-title {\n  font-size: $font-size-base;\n  margin: 0;\n}\n\n.dropdown-icon {\n  &::after {\n    margin-left: 0;\n  }\n}\n\n// Dropdown Sizes\n.dropdown-menu-lg {\n  max-width: 300px;\n  min-width: 280px;\n  padding: 0;\n\n  .dropdown-divider {\n    margin: 0;\n  }\n\n  .dropdown-item {\n    padding: $dropdown-padding-y $dropdown-item-padding-x;\n  }\n\n  p {\n    margin: 0;\n    white-space: normal;\n  }\n}\n\n// Dropdown Submenu\n.dropdown-submenu {\n  position: relative;\n\n  > a::after {\n    @include caret-right ();\n    float: right;\n    margin-left: .5rem;\n    margin-top: .5rem;\n  }\n\n  > .dropdown-menu {\n    left: 100%;\n    margin-left: 0;\n    margin-top: 0;\n    top: 0;\n  }\n}\n\n// Dropdown Hover\n.dropdown-hover {\n  &:hover,\n  &.nav-item.dropdown:hover,\n  .dropdown-submenu:hover,\n  &.dropdown-submenu:hover {\n    > .dropdown-menu {\n      display: block;\n    }\n  }\n}\n\n\n// Dropdown Sizes\n.dropdown-menu-xl {\n  max-width: 420px;\n  min-width: 360px;\n  padding: 0;\n\n  .dropdown-divider {\n    margin: 0;\n  }\n\n  .dropdown-item {\n    padding: $dropdown-padding-y $dropdown-item-padding-x;\n  }\n\n  p {\n    margin: 0;\n    white-space: normal;\n  }\n}\n\n// Dropdown header and footer\n.dropdown-footer,\n.dropdown-header {\n  display: block;\n  font-size: $font-size-sm;\n  padding: .5rem $dropdown-item-padding-x;\n  text-align: center;\n}\n\n// Add fade animation to dropdown menus by appending\n// the class .animated-dropdown-menu to the .dropdown-menu ul (or ol)\n.open:not(.dropup) > .animated-dropdown-menu {\n  @include animation(flipInX .7s both);\n  backface-visibility: visible !important;\n}\n\n// Fix dropdown menu in navbars\n.navbar-custom-menu > .navbar-nav {\n  > li {\n    position: relative;\n    > .dropdown-menu {\n      position: absolute;\n      right: 0;\n      left: auto;\n    }\n  }\n}\n\n@include media-breakpoint-down(sm) {\n  .navbar-custom-menu > .navbar-nav {\n    float: right;\n    > li {\n      position: static;\n      > .dropdown-menu {\n        position: absolute;\n        right: 5%;\n        left: auto;\n        border: 1px solid #ddd;\n        background-color: $white;\n      }\n    }\n  }\n}\n\n// User Menu\n.navbar-nav > .user-menu {\n  > .nav-link::after {\n    content: none;\n  }\n\n  > .dropdown-menu {\n    @include border-top-radius(0);\n    padding: 0;\n    width: 280px;\n\n    &,\n    > .user-body {\n      @include border-bottom-radius(4px);\n    }\n\n    // Header menu\n    > li.user-header {\n      height: 175px;\n      padding: 10px;\n      text-align: center;\n\n      // User image\n      > img {\n        z-index: 5;\n        height: 90px;\n        width: 90px;\n        border: 3px solid;\n        border-color: transparent;\n        border-color: rgba(255, 255, 255, .2);\n      }\n\n      > p {\n        z-index: 5;\n        font-size: 17px;\n        //text-shadow: 2px 2px 3px #333333;\n        margin-top: 10px;\n\n        > small {\n          display: block;\n          font-size: 12px;\n        }\n      }\n    }\n\n    // Menu Body\n    > .user-body {\n      @include clearfix ();\n      border-bottom: 1px solid $gray-700;\n      border-top: 1px solid $gray-300;\n      padding: 15px;\n\n      a {\n        @include media-breakpoint-up(sm) {\n          background-color: $white !important;\n          color: $gray-700 !important;\n        }\n      }\n    }\n\n    // Menu Footer\n    > .user-footer {\n      @include clearfix ();\n      background-color: $gray-100;\n      padding: 10px;\n\n      .btn-default {\n        color: $gray-600;\n\n        &:hover {\n          @include media-breakpoint-up(sm) {\n            background-color: $gray-100;\n          }\n        }\n      }\n    }\n  }\n\n  .user-image {\n    @include media-breakpoint-up(sm) {\n      float: none;\n      line-height: 10px;\n      margin-right: .4rem;\n      margin-top: -8px;\n    }\n\n    border-radius: 50%;\n    float: left;\n    height: $sidebar-user-image-width;\n    margin-right: 10px;\n    margin-top: -2px;\n    width: $sidebar-user-image-width;\n  }\n}\n\n@include dark-mode () {\n  .dropdown-menu {\n    background-color: $dark;\n    color: $white;\n  }\n  .dropdown-item {\n    color: $white;\n\n    &:focus,\n    &:hover {\n      background-color: lighten($dark, 5%);\n    }\n  }\n  .dropdown-divider {\n    border-color: $gray-600;\n  }\n\n  .navbar-nav > .user-menu > .dropdown-menu {\n    > .user-footer {\n      background-color: lighten($dark, 2.5%);\n      color: $white;\n\n      .btn-default {\n        color: $white;\n\n        &:hover,\n        &:focus {\n          background-color: lighten($dark, 5%);\n          color: $gray-300;\n        }\n        &:focus {\n          background-color: lighten($dark, 7.5%);\n        }\n      }\n    }\n    > .user-body {\n      border-color: $gray-600;\n    }\n    > .user-body a {\n      background-color: transparent !important;\n      color: $white !important;\n\n      &:hover,\n      &:focus {\n        color: $gray-400 !important;\n      }\n    }\n  }\n}\n","//\n// Component: Nav\n//\n\n.nav-pills {\n  .nav-link {\n    color: $gray-600;\n\n    &:not(.active):hover {\n      color: theme-color(\"primary\");\n    }\n  }\n\n  .nav-item {\n    &.dropdown.show {\n      .nav-link:hover {\n        color: $dropdown-link-active-color;\n      }\n    }\n  }\n}\n\n// Vertical Tabs\n.nav-tabs.flex-column {\n  border-bottom: 0;\n  border-right: $nav-tabs-border-width solid $nav-tabs-border-color;\n\n  .nav-link {\n    border-bottom-left-radius: $nav-tabs-border-radius;\n    border-top-right-radius: 0;\n    margin-right: -$nav-tabs-border-width;\n\n    @include hover-focus () {\n      border-color: $gray-200 transparent $gray-200 $gray-200;\n    }\n  }\n\n  .nav-link.active,\n  .nav-item.show .nav-link {\n    border-color: $gray-300 transparent $gray-300 $gray-300;\n  }\n\n  &.nav-tabs-right {\n    border-left: $nav-tabs-border-width solid $nav-tabs-border-color;\n    border-right: 0;\n\n    .nav-link {\n      border-bottom-left-radius: 0;\n      border-bottom-right-radius: $nav-tabs-border-radius;\n      border-top-left-radius: 0;\n      border-top-right-radius: $nav-tabs-border-radius;\n      margin-left: -$nav-tabs-border-width;\n\n      @include hover-focus () {\n        border-color: $gray-200 $gray-200 $gray-200 transparent;\n      }\n    }\n\n    .nav-link.active,\n    .nav-item.show .nav-link {\n      border-color: $gray-300 $gray-300 $gray-300 transparent;\n    }\n  }\n}\n\n.navbar-no-expand {\n  flex-direction: row;\n\n  .nav-link {\n    padding-left: $navbar-nav-link-padding-x;\n    padding-right: $navbar-nav-link-padding-x;\n  }\n\n  .dropdown-menu {\n    position: absolute;\n  }\n}\n\n// Color variants\n@each $color, $value in $theme-colors {\n  @if $color == dark or $color == light {\n    .navbar-#{$color} {\n      background-color: $value;\n      @if $color == dark {\n        border-color: lighten($dark, 10%);\n      }\n    }\n  }\n}\n\n@each $color, $value in $theme-colors {\n  @if $color != dark and $color != light {\n    @include navbar-variant($color, $value);\n  }\n}\n\n@each $color, $value in $colors {\n  @include navbar-variant($color, $value);\n}\n\n.navbar-nav-not-expanded {\n  flex-direction: row;\n\n  .dropdown-menu {\n    position: absolute;\n  }\n\n  .nav-link {\n    padding-right: $navbar-nav-link-padding-x;\n    padding-left: $navbar-nav-link-padding-x;\n  }\n}\n\n\n@include dark-mode () {\n  .nav-pills .nav-link {\n    color: $gray-400;\n  }\n\n  .nav-tabs {\n    border-color: lighten($dark, 15%);\n\n    .nav-link:focus,\n    .nav-link:hover {\n      border-color: lighten($dark, 15%);\n    }\n\n    .nav-item.show .nav-link,\n    .nav-link.active {\n      background-color: $dark;\n      border-color: lighten($dark, 15%) lighten($dark, 15%) transparent lighten($dark, 15%);\n      color: $white;\n    }\n\n    &.flex-column {\n      .nav-item.show .nav-link,\n      .nav-link {\n        &.active,\n        &:focus,\n        &:hover {\n          border-color: lighten($dark, 15%) transparent lighten($dark, 15%) lighten($dark, 15%);\n        }\n        &:focus,\n        &:hover {\n          background-color: lighten($dark, 5%);\n        }\n      }\n      &.nav-tabs-right {\n        border-color: lighten($dark, 15%);\n        .nav-link {\n          &.active,\n          &:focus,\n          &:hover {\n            border-color: lighten($dark, 15%) lighten($dark, 15%) lighten($dark, 15%) transparent;\n          }\n        }\n      }\n    }\n  }\n\n  // Color variants\n  @each $color, $value in $theme-colors-alt {\n    @if $color == dark or $color == light {\n      .navbar-#{$color} {\n        background-color: $value;\n        @if $color == dark {\n          border-color: lighten($dark, 10%);\n        }\n      }\n    }\n  }\n\n  @each $color, $value in $theme-colors-alt {\n    @if $color != dark and $color != light {\n      @include navbar-variant($color, $value);\n    }\n  }\n\n  @each $color, $value in $colors-alt {\n    @include navbar-variant($color, $value);\n  }\n}\n","//\n// Mixins: Navbar\n//\n\n// Navbar Variant\n@mixin navbar-variant($name, $color) {\n  .navbar-#{$name} {\n    background-color: $color;\n    color: color-yiq($color);\n\n    &.navbar-light {\n      .form-control-navbar {\n        &::placeholder {\n          color: rgba($gray-800, .8);\n        }\n        &,\n        + .input-group-append > .btn-navbar {\n          background-color: darken($color, 4%);\n          border-color: darken($color, 9%);\n          color: rgba($gray-800, .8);\n        }\n\n        &:focus {\n          &::placeholder {\n            color: $gray-800;\n          }\n          &,\n          + .input-group-append .btn-navbar {\n            background-color: darken($color, 5%);\n            border-color: darken($color, 9%) !important;\n            color: $gray-800;\n          }\n        }\n      }\n    }\n    &.navbar-dark {\n      .form-control-navbar {\n        &::placeholder {\n          color: rgba($white, .8);\n        }\n        &,\n        + .input-group-append > .btn-navbar {\n          background-color: lighten($color, 4%);\n          border-color: lighten($color, 9%);\n          color: rgba($white, .8);\n        }\n\n        &:focus {\n          &::placeholder {\n            color: $white;\n          }\n          &,\n          + .input-group-append .btn-navbar {\n            background-color: lighten($color, 5%);\n            border-color: lighten($color, 9%) !important;\n            color: $white;\n          }\n        }\n      }\n    }\n  }\n}\n","//\n// Component: Pagination\n//\n\n.pagination-month {\n  .page-item {\n    justify-self: stretch;\n\n    .page-link {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      flex-direction: column;\n      box-shadow: none;\n    }\n    &:first-child,\n    &:last-child {\n      .page-link {\n        height: 100%;\n        font-size: $font-size-lg;\n      }\n    }\n    .page-month {\n      margin-bottom: 0;\n      font-size: $font-size-lg;\n      font-weight: $font-weight-bold;\n    }\n    .page-year {\n      margin-bottom: 0;\n    }\n  }\n\n  &.pagination-lg {\n    .page-month {\n      font-size: ($font-size-lg * 1.25);\n    }\n  }\n  &.pagination-sm {\n    .page-month {\n      font-size: ($font-size-base);\n    }\n  }\n}\n\n@include dark-mode () {\n  .page-item {\n    &.disabled a,\n    &.disabled .page-link {\n      background-color: lighten($dark, 2.5%) !important;\n      border-color: $gray-600 !important;\n      color: $gray-600;\n    }\n\n    .page-link {\n      color: $primary-alt;\n    }\n\n    &.active {\n      .page-link {\n        background-color: $primary-alt;\n        border-color: $primary-alt;\n        color: $white;\n\n        &:hover,\n        &:focus {\n          color: $gray-400 !important;\n        }\n      }\n    }\n    &:not(.active) {\n      .page-link {\n        background-color: $dark;\n        border-color: $gray-600;\n\n        &:hover,\n        &:focus {\n          color: lighten($primary-alt, 5%);\n          background-color: lighten($dark, 5%);\n        }\n      }\n    }\n  }\n}\n","//\n// Component: Forms\n//\n\n.form-group {\n  &.has-icon {\n    position: relative;\n\n    .form-control {\n      padding-right: 35px;\n    }\n\n    .form-icon {\n      background-color: transparent;\n      border: 0;\n      cursor: pointer;\n      font-size: 1rem;\n      // margin-top: -3px;\n      padding: $input-btn-padding-y $input-btn-padding-x;\n      position: absolute;\n      right: 3px;\n      top: 0;\n    }\n  }\n}\n\n// Button groups\n.btn-group-vertical {\n  .btn {\n    &.btn-flat:first-of-type,\n    &.btn-flat:last-of-type {\n      @include border-radius(0);\n    }\n  }\n}\n\n// Support icons in form-control\n.form-control-feedback {\n  &.fa,\n  &.fas,\n  &.far,\n  &.fab,\n  &.fal,\n  &.fad,\n  &.svg-inline--fa,\n  &.ion {\n    line-height: $input-height;\n  }\n}\n\n.input-lg + .form-control-feedback,\n.input-group-lg + .form-control-feedback {\n  &.fa,\n  &.fas,\n  &.far,\n  &.fab,\n  &.fal,\n  &.fad,\n  &.svg-inline--fa,\n  &.ion {\n    line-height: $input-height-lg;\n  }\n}\n\n.form-group-lg {\n  .form-control + .form-control-feedback {\n    &.fa,\n    &.fas,\n    &.far,\n    &.fab,\n    &.fal,\n    &.fad,\n    &.svg-inline--fa,\n    &.ion {\n      line-height: $input-height-lg;\n    }\n  }\n}\n\n.input-sm + .form-control-feedback,\n.input-group-sm + .form-control-feedback {\n  &.fa,\n  &.fas,\n  &.far,\n  &.fab,\n  &.fal,\n  &.fad,\n  &.svg-inline--fa,\n  &.ion {\n    line-height: $input-height-sm;\n  }\n}\n\n.form-group-sm {\n  .form-control + .form-control-feedback {\n    &.fa,\n    &.fas,\n    &.far,\n    &.fab,\n    &.fal,\n    &.fad,\n    &.svg-inline--fa,\n    &.ion {\n      line-height: $input-height-sm;\n    }\n  }\n}\n\nlabel:not(.form-check-label):not(.custom-file-label) {\n  font-weight: $font-weight-bold;\n}\n\n.warning-feedback {\n  @include font-size($form-feedback-font-size);\n  color: theme-color(\"warning\");\n  display: none;\n  margin-top: $form-feedback-margin-top;\n  width: 100%;\n}\n\n.warning-tooltip {\n  @include border-radius($form-feedback-tooltip-border-radius);\n  @include font-size($form-feedback-tooltip-font-size);\n  background-color: rgba(theme-color(\"warning\"), $form-feedback-tooltip-opacity);\n  color: color-yiq(theme-color(\"warning\"));\n  display: none;\n  line-height: $form-feedback-tooltip-line-height;\n  margin-top: .1rem;\n  max-width: 100%; // Contain to parent when possible\n  padding: $form-feedback-tooltip-padding-y $form-feedback-tooltip-padding-x;\n  position: absolute;\n  top: 100%;\n  z-index: 5;\n}\n\n.form-control {\n  &.is-warning {\n    border-color: 